.notify {
  background-color: #e52b50;
  color: #ffffff;
  padding: 8px 16px;
  border-radius: 3px;
  transition: all 200ms ease;
  font-weight: 500;
  text-align: center;
  display: inline-block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 16px;
  line-height: 24px;
  font-style: normal;
  min-width: 180px;
  position: absolute;
  right: 20%;
  top: 5%;
}