.appear {
  opacity: 0;
  transform: translateX(100%);
}

.appearActive {
  opacity: 1;
  transform: translateX(0);
  transition: transform 250ms linear;
}

.enter {
  opacity: 0;
  transform: translateX(100%);
}

.enterActive {
  opacity: 1;
  transform: translateX(0);
  transition: transform 250ms linear;
}

.exit {
  opacity: 1;
  transform: translateX(0);
}

.exitActive {
  opacity: 0;
  transform: translateX(100%);
  transition: transform 250ms linear;
}